<template>
    <div class="demo-block">
         <h2 class="demo-title"></h2>
         <div class="demo-body">
             <in-form inline>
                 <in-form-item label="">
                     <in-input placeholder="" v-model="form.title"></in-input>
                 </in-form-item>
                 <in-form-item label="">
                     <in-textarea placeholder="" v-model="form.description"></in-textarea>
                 </in-form-item>
                 <in-form-item label="">
                     <in-select v-model="form.type">
                        <in-option :value="1" label=""></in-option>
                        <in-option :value="2" label=""></in-option>
                        <in-option :value="3" label=""></in-option>
                     </in-select>
                 </in-form-item>
                 <in-form-item label="">
                    <in-datepicker style="width:200px;" v-model="form.startDate"></in-datepicker>
                    <in-datepicker style="width:200px; margin-left:18px;" v-model="form.endDate"></in-datepicker>
                 </in-form-item>
                 <in-form-item label="" >
                     <in-number style="width:200px;" v-model="form.number" :min="1"></in-number>
                 </in-form-item>
                 <in-form-item label="">
                     <in-switch v-model="form.isSole"></in-switch>
                 </in-form-item>
                 <in-form-item label="">
                     <in-checkbox-group v-model="form.channel">
                        <in-checkbox v-for="item in channelArray" :key="item.value" :label="item.value">
                            {{item.name}}
                        </in-checkbox>
                     </in-checkbox-group>
                 </in-form-item>
                 <in-form-item>
                     <in-button type="primary" @click="submitClick"></in-button>
                     <in-tip content="">
                         <in-button></in-button>
                     </in-tip>
                 </in-form-item>
             </in-form>
         </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            channelArray:[{name:'',value:1},{name:'',value:2},{name:'',value:3},{name:'B',value:4}],
            form:{
                title:'',
                description:'',
                type:2,
                startDate:'2018-06-05',
                endDate:'2018-06-05',
                number:1,
                isSole:true,
                channel:[]
            }
        }
    },
    methods:{
        submitClick(){
            this.$message(JSON.stringify(this.form));
        }
    }
}
</script>

<style lang="less" scoped>
.demo-block{
    width: 500px;
}
</style>




