<template>
    <div class="view-main">
        <div class="demo-block">
            <h2 class="demo-title">Popover</h2>
            <div class="demo-body" style="padding:100px;">
                <in-popover width="400" placement="top">
                    <in-button>top</in-button>
                    <div slot="content">
                        <p>,,,</p>
                    </div>
                </in-popover>
                <in-popover width="200" placement="bottom">
                    <in-button>bottom</in-button>
                    <div slot="content">
                        <p>,,,</p>
                    </div>
                </in-popover>
                <in-popover width="300" placement="left">
                    <in-button>left</in-button>
                    <div slot="content">
                        <p>,,,</p>
                    </div>
                </in-popover>
                <in-popover width="300" placement="right">
                    <in-button>right</in-button>
                    <div slot="content">
                        <p>,,,</p>
                    </div>
                </in-popover>
            </div>
        </div>
        <!--dropdown-->
        <div class="demo-block">
            <h2 class="demo-title"></h2>
            <div class="demo-body">
                <in-dropdown @command="command">
                   <in-button></in-button>
                   <in-dropdown-menu slot="dropdown">
                       <in-dropdown-item command="a"></in-dropdown-item>
                       <in-dropdown-item command="b"></in-dropdown-item>
                       <in-dropdown-item command="c"></in-dropdown-item>
                   </in-dropdown-menu>
                </in-dropdown>

                <in-dropdown>
                   <in-button type="text">zhushunsheng@ingtube.com</in-button>
                   <in-dropdown-menu slot="dropdown">
                       <in-dropdown-item></in-dropdown-item>
                       <in-dropdown-item></in-dropdown-item>
                       <in-dropdown-item></in-dropdown-item>
                   </in-dropdown-menu>
                </in-dropdown>
            </div>
        </div>
        <!--dropdown-->
        <!--  -->
        <div class="demo-block">
            <h2 class="demo-title"></h2>
            <div class="demo-body">
                <in-button @click="clickOne"></in-button>
                
                <in-button  @click="primaryClick" type="primary"></in-button>

                <in-button round @click="confirmClick"></in-button>
                <in-button round type="primary"></in-button>

                <in-button :disabled="true"></in-button>
                <in-button disabled type="primary"></in-button>

                <in-button type="text"></in-button>
                <in-button disabled type="text"></in-button>
            </div>
        </div>
        <!-- / -->
        <div class="demo-block">
            <h2 class="demo-title"></h2>
            <div class="demo-body">
                <div class="demo-input">
                    <in-input v-model="form.text"
                              @focus="focus"
                              @blur="blur"
                              @change="change">
                    </in-input>
                </div>
                <div class="demo-input">
                    <in-input disabled  v-model="form.text"></in-input>
                </div>
            </div>
            <h2 class="demo-title"></h2>
            <div class="demo-body">
                <div class="demo-input">
                    <in-number v-model="form.number" :step="5" :min="1" :max="100"></in-number>
                </div>
                <div class="demo-input">
                    <in-number v-model="form.number"></in-number>
                </div>
                <div class="demo-input">
                    <in-number disabled v-model="form.number"></in-number>
                </div>
            </div>
             <h2 class="demo-title"></h2>
            <div class="demo-body">
                <div class="demo-input">
                    <in-textarea v-model="form.textArea"
                                 @change="textareaChange"
                                 @blur="textareaBlur"></in-textarea>
                </div>
                <div class="demo-input">
                    <in-textarea disabled v-model="form.textArea"></in-textarea>
                </div>
            </div>
        </div>

        <div class="demo-block">
            <h2 class="demo-title"></h2>
            <div class="demo-body">
                 <in-tip content="">
                    <span></span>
                </in-tip>
            </div>
        </div>

        <select-demo></select-demo>

        <pagination-demo></pagination-demo>

        <model-demo></model-demo>

        <switch-demo></switch-demo>
    </div>
</template>
<script>
import SelectDemo from './SelectDemo'
import PaginationDemo from './PaginationDemo'
import ModelDemo from './ModelDemo'
import SwitchDemo from './SwitchDemo'
export default {
    components:{
        SelectDemo,
        PaginationDemo,
        ModelDemo,
        SwitchDemo
    },
    data(){
        return {
            form:{
                text:'',
                number:10,
                textArea:''
            }
        }
    },
    methods:{
        command(command){
           this.$message(command);
        },
        clickOne(e){
            this.$message(JSON.stringify(this.form));
        },
        focus(e){
            console.log(e.type);
        },
        blur(e){
            console.log(e.type);
        },
        change(e){
            console.log(e.type);
        },
        textareaChange(e){
            console.log(e.type);
        },
        textareaBlur(e){
            console.log(e.type);
        },
        primaryClick(){
            this.$message("");
        },
        confirmClick(e){
            this.$confirm({
                title:'',
                text:'',
                confirmButton:'',
                cancelButton:'',
                action:(res)=>{
                    if(res == 'confirm'){
                        this.$message("");
                    }else{
                        this.$confirm({
                            action:(res)=>{
                                 this.$message("");
                            }
                        })
                    }
                }
            })
        }
    }
}
</script>
<style lang="less" scoped>
.view-main{
    padding: 0;
    .demo-input{
        display: inline-block;
        vertical-align: top;
        width: 300px;
    }
}
</style>





