<template>
    <div class="demo-block">
        <h2 class="demo-title">model</h2>
        <div class="demo-body">
            <in-button @click="btnClick"></in-button>
        </div>
        <!--model-->
        <in-model title="" v-model="isShow">
            <div class="form">
                <div class="form-item">
                    <in-input></in-input>
                </div>
                <div class="form-item">
                    <in-input></in-input>
                </div>
                <div class="form-item">
                    <in-select v-model="select">
                        <in-option v-for="item in selectArray" :key="item" :label="item" :value="item"></in-option>
                    </in-select>
                </div>
                <div class="form-item">
                    <in-button type="primary" @click="loginClick"></in-button>
                    <in-button @click="isShow = false"></in-button>
                </div>
            </div>
        </in-model>
        <!--/model-->
        <in-model title="" v-model="isShow2">
            <p></p>
        </in-model>
    </div>
</template>
<script>
export default {
    data(){
        return {
            isShow:false,
            isShow2:false,
            select:'',
            selectArray:['','','']
        }
    },
    methods:{
        btnClick(){
            this.isShow = true;
        },
        loginClick(){
            this.isShow2 = true;
        }
    }
}
</script>
<style lang="less" scoped>
.form{
    >.form-item{
        &+.form-item{
            margin-top: 24px;
        }
    }
}
</style>





