<template>
    <div class="demo-block">
        <h2 class="demo-title">Tab</h2>
        <in-tab>
            <in-tab-panel label="">
               <in-form :inline="true">
                   <in-form-item label="" >
                       <in-switch v-model="form.solo"></in-switch>
                   </in-form-item>
               </in-form>
            </in-tab-panel>
            <in-tab-panel label="EMV">
                <in-form>
                   <in-form-item label="EMV">
                       <in-number v-model="form.emv" :min="50" :max="2000"></in-number>
                   </in-form-item>
               </in-form>
            </in-tab-panel>
            <in-tab-panel label="">
                <in-form :inline="true">
                   <in-form-item label="">
                       <in-number v-model="form.number" :min="50" :max="2000"></in-number>
                   </in-form-item>
               </in-form>
            </in-tab-panel>
        </in-tab>  
    </div>
</template>
<script>
export default {
    data(){
        return {
            form:{
                solo:true,
                emv:1000,
                number:100
            }
        }
    }
}
</script>




