<template>
    <div class="demo-block">
        <in-table :data="list">
            <template slot-scope="scope">
                <in-table-column label="">
                        {{scope.row.nickName}}
                </in-table-column>
                <in-table-column label="">
                    <in-tip :content="scope.row.nickName">
                        <span>{{scope.row.plat}}</span>
                    </in-tip>
                    
                </in-table-column>
                <in-table-column label="">
                    {{scope.row.code}}
                </in-table-column>
                <in-table-column label="">
                     {{scope.row.status}}
                </in-table-column>
                <in-table-column label="" width="200">
                    <in-button type="primary" @click="passClick"></in-button>
                    <in-button @click="refuseClick"></in-button>
                </in-table-column>
            </template>
        </in-table>
        <div class="pagination">
            <in-pagination :total="50"></in-pagination>
        </div>
        
    </div>
</template>
<script>
export default {
    data(){
        return {
            list:[{
                nickName:'',
                plat:'',
                code:'INGVIP0',
                status:''
            },{
                nickName:'0123123',
                plat:'',
                code:'INGVIP30',
                status:''
            }]
        }
    },
    methods:{
        passClick(){
            this.$message("");
        },
        refuseClick(){
            this.$confirm({
                title:'',
                text:'',
                confirmButton:'',
                cancelButton:'',
                action:(res)=>{
                    if(res == 'confirm'){
                        this.$message("");
                    }else{
                        return false;
                    }
                }
            })
        }
    }
}
</script>
<style lang="less" scoped>
.pagination{
    margin-top: 30px;
    text-align: center;
}
</style>





