<template>
    <div class="demo-block">
        <h2 class="demo-title"></h2>
        <div class="demo-body">
            <div class="demo-select">
                 <in-select v-model="form.select1" @change="change">
                     <in-option v-for="item in selectArray1" :key="item" :label="item" :value="item"></in-option>
                 </in-select>
            </div>
            <div class="demo-select">
                 <in-select v-model="form.select2">
                     <in-option v-for="item in selectArray2" :key="item" :label="item" :value="item"></in-option>
                 </in-select>
            </div>
            <div class="demo-select">
                 <in-select disabled v-model="form.select2">
                     <in-option v-for="item in selectArray2" :key="item" :label="item" :value="item"></in-option>
                 </in-select>
            </div>
            <div class="demo-button">
                <in-button @click="btnClick"></in-button>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            selectArray1:["","","",""],
            selectArray2:["","","ipa","","","U",""],
            form:{
                select1:"",
                select2:""
            }
        }
    },
    methods:{
        btnClick(){
            this.$message(JSON.stringify(this.form));
        },
        change(e){
            this.$message('change:'+this.form.select1);
        }
    }
}
</script>

<style lang="less" scoped>
.demo-block{
    .demo-select{
        width: 300px;
        display: inline-block;
        vertical-align: top;
    }
    .demo-button{
        display: inline-block;
        vertical-align: top;
    }
}
</style>



