<template>
    <div class="demo-block">
        <h2 class="demo-block">/</h2>
        <div class="demo-body">
            <in-switch disabled v-model="form.isShow"></in-switch>
            <in-switch v-model="form.isShow"></in-switch>
            <in-switch v-model="form.isShow"></in-switch>
            <span>{{form.isShow}}</span>
        </div>
        <div class="demo-body">
            <in-checkbox v-model="form.checkbox" label=""></in-checkbox>
            <in-checkbox v-model="form.checkbox" label=""></in-checkbox>
            <in-checkbox disabled v-model="form.checkbox2" label=""></in-checkbox>
        </div>
        <div class="demo-body">
            <in-checkbox-group v-model="form.hobby">
                <in-checkbox v-for="item in hobbyList" :key="item" :label="item"></in-checkbox>
            </in-checkbox-group>
            <in-checkbox-group v-model="form.hobby">
                <in-checkbox v-for="item in hobbyList" :key="item" :label="item"></in-checkbox>
            </in-checkbox-group>
        </div>
        <div class="demo-body">
            <in-radio v-model="form.gender" :label="1"></in-radio>
            <in-radio v-model="form.gender" :label="0"></in-radio>
            <in-radio disabled v-model="form.gender" :label="2"></in-radio>
        </div>
        <div class="demo-body">
            <in-button @click="click"></in-button>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            hobbyList:["","","",""],
            form:{
                isShow:true,
                checkbox:false,
                checkbox2:true,
                gender:0,
                hobby:[""]
            }
        }
    },
    methods:{
        click(){
            this.$message(JSON.stringify(this.form));
        }
    }
}
</script>




